export default {
	props: {
		value: {
			type: Boolean,
			default: false,
		},
		position: { //弹窗出现位置 top left right bottom
			type: String,
			default: 'center',
		},
		overlay: { //是否显示蒙版
			type: Boolean,
			default: true,
		},
		overlayCilck: {
			type: Boolean,
			default: true,
		}
	},
	data() {
		return {
			isShow: this.value,
		}
	},
	methods: {
		close() {
			if (this.overlayCilck) {
				this.isShow = false;
				this.value = false;
				this.$emit('input', false);
			} else {
				return;
			}

		}
	},
	watch: {
		value(newValue) {
			this.isShow = newValue;
		},
	},
	mounted() {

	}
}
